

cc.Class({
    extends: cc.Component,

    properties: 
    {
        //这个属性引用了星星预制体资源
        starPrefab :
        {
            //无默认值
            default: null,
            //类型 预制体
            type: cc.Prefab
        },

        //时间范围
        maxStarDuration: 0,
        minStarDuration: 0,
        
        //获取地面节点
        ground: 
        {
            default: null,
            type: cc.Node
        },

        //获取主角
        player: 
        {
            default: null,
            type: cc.Node
        },

        //文本对象
        scoreDisplay:
        {
            default: null,
            type: cc.Label
        },

        //音源对象
        scoreAudio:
        {
            default: null,
            type: cc.AudioClip
        },
        

    },

     onLoad () 
     {
        //获取地面Y轴值
        this.groundY = this.ground.y + this.ground.height / 2;

        
        //取消计时器与有效值的注释 玩家将不再跳动
        //初始化计时器
        this.timer = 0;
        //计时有效值
        this.starDuration = 0;
        
         
        //调用方法 生成新星
        this.spawnNewStar();
        //初始化分数
        this.score = 0;

     },

     //#region  生成新星方法
    spawnNewStar()
    {
        //类型 对象n = 类.生成(传入的物体)
        var newStar = cc.instantiate(this.starPrefab);
        //将对象n添加到 Canvas 节点下
        this.node.addChild(newStar);
        //设置对象n基于父物体的位置(XY或vec2)
        newStar.setPosition(this.getNewStarPosition());

        //在星星组件上暂存 game 引用
        newStar.getComponent('Star').game = this;

        //!!!!!接触星星不消失反而增多问题关键点
        //重置计时器，根据消失时间范围随机取一个值  大概是延续游戏时间
        this.starDuration = this.minStarDuration + Math.random() * (this.maxStarDuration - this,minStarDuration);
        this.timer = 0;
    },
//#endregion

getNewStarPosition ()
{
    //横轴归零
    var randX = 0;
    //纵轴 = 地面的Y轴 + 数学.随机() * 玩家人物.'Player'脚本.跳跃高度值 + 50 
    var randY = this.groundY + Math.random() * this.player.getComponent('Player_script').jumpHeight + 50;

    //根据屏幕宽度 随机得到一个X坐标
    var maxX = this.node.width / 2;
    randX = (Math.random() - 0.5) * 2 * maxX;

    //返回XY轴值
    return cc.v2(randX,randY);
},

     update (dt) 
     {
         
         //大概是在随机时间内要接触到星星 超过时限则游戏失败
         if(this.timer > this.starDuration)
         {
             this.gameOver();
             this.enabled = false;
             return;
         }
         this.timer += dt;

         /*
         // 每帧更新计时器，超过限度还没有生成新的星星
        // 就会调用游戏失败逻辑
        if (this.timer > this.starDuration) {
            this.gameOver();
            this.enabled = false;   // disable gameOver logic to avoid load scene repeatedly
            return;
        }
        this.timer += dt;
        */
        
         
     },


    //更新得分显示方法
    gainScore()
    {
        this.score += 1;
        this.scoreDisplay.string = 'Score: ' + this.score;
        
        //播放得分音效
        cc.audioEngine.playEffect(this.scoreAudio,false);

        /*
        this.score += 1;
        // 更新 scoreDisplay Label 的文字
        this.scoreDisplay.string = 'Score: ' + this.score;
        // 播放得分音效
        cc.audioEngine.playEffect(this.scoreAudio, false);
        */
    },

    
    //游戏结束方法
    gameOver()
    {
        //player对象的Action停止
        this.player.stopAllActions();
        //加载游戏场景(场景名);
        cc.director.loadScene('game');        
    },
    

});
